import React, { Component } from 'react';
import Parser from 'html-react-parser';

class Home extends Component {
  state = {
    myAsset: '',
    result: ''
  }

  assetChange = (e) => {
    this.setState({myAsset: e.target.value});
  }

  actionCompute = (e) => {
    if (e.type !== 'click' && e.key !== 'Enter') {
      return;
    }

    let myAsset = parseInt(this.state.myAsset);

    if (!/^[0-9]*$/.test(myAsset)) {
      alert('0이상의 정수만 입력해주세요.');
      return;
    }

    let group = 0;

    const koreaPeople = 51843195;
    const percentile = [
      0, // 100%
      10700000, // 90%
      43310000, // 80%
      89500000, // 70%
      140650000, // 60%
      200500000, // 50%
      274960000, // 40%
      369140000, // 30%
      517000000, // 20%
      810120000, // 10%
      1400000000, // 5%
      3000000000, // 1%
      1240000000000, // 36위 0.000001 %
      19840000000000 // 1위
    ]

    percentile.forEach((o, i) => {
      if (myAsset > o) {
        group++;
      }
    });

    const newFormat = new Intl.NumberFormat();
    let max = percentile[group] - percentile[group - 1];
    let val = max - (myAsset - percentile[group - 1]);
    let percent = 0;
    let ranking = 0;

    if (group === 0) {
      this.setState({result: '당신의 순자산은 대한민국 상위 <strong>100%</strong> 입니다'});
    } else if (group < 10) {
      percent = ((10 - group) * 10 + val / max * 10);
      ranking = parseInt(koreaPeople * percent / 100);
      this.setState({result: `당신의 순자산은 대한민국 상위 <strong>${percent.toFixed(2)}%</strong> 입니다<br>순위는 <strong>${newFormat.format(ranking)}위</strong> 이며<br>순위 아래로는 <strong>${newFormat.format(koreaPeople - ranking)}명</strong>이 있습니다.`});
    } else {
      if (group === 10) { // 5 ~ 10 %
        percent = (5 + val / max * 5);
        ranking = parseInt(koreaPeople * percent / 100);
        this.setState({result: `당신의 순자산은 대한민국 상위 <strong>${percent.toFixed(2)}%</strong> 입니다<br>순위는 <strong>${newFormat.format(ranking)}위</strong> 이며<br>순위 아래로는 <strong>${newFormat.format(koreaPeople - ranking)}명</strong>이 있습니다.`});
      }
      if (group === 11) { // 1 ~ 5 %
        percent = (1 + val / max * 4);
        ranking = parseInt(koreaPeople * percent / 100);
        this.setState({result: `당신의 순자산은 대한민국 상위 <strong>${percent.toFixed(2)}%</strong> 입니다<br>순위는 <strong>${newFormat.format(ranking)}위</strong> 이며<br>순위 아래로는 <strong>${newFormat.format(koreaPeople - ranking)}명</strong>이 있습니다.`});
      }
      if (group === 12) { // 36위 ~ 1 %
        this.setState({result: `당신의 순자산은 대한민국 상위 <strong>1%</strong> 입니다.`});
      }
      if (group === 13) { // 2위 ~ 36위
        this.setState({result: `당신의 순자산은 대한민국 상위 <strong>0.000001%</strong> 입니다.`});
      }
      if (group === 14) { // 1위
        this.setState({result: `당신의 순자산은 대한민국 상위 <strong>1위</strong> 입니다.`});
      }
    }
  }

  render() {
    return (
      <div id="asset">
        <h2>대한민국 순자산 순위 구하기</h2>
        <div className="bothbox">
          <input
            type="number"
            value={this.state.myAsset}
            onChange={this.assetChange}
            onKeyPress={this.actionCompute}
            placeholder="순자산을 입력해주세요"
          />
          <button type="button" onClick={this.actionCompute}>계산하기</button>
        </div>
        <p className="result">{Parser(this.state.result)}</p>
        <p className="notice">※ 위 결과는 통계청 자료를 기반으로 근사치를 구한수치입니다.</p>
      </div>
    );
  }
}

export default Home;